body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.fh{
  height:100vh;
  z-index: -1;
}
.header{
  z-index:1;
  padding:10px;
  position: relative;
}
.nav-item{
  display:inline-block;
 
}
.nav{
  display:none;
  
}
.helpText{
  color:rgb(78, 70, 70);
  font-weight: 600;
  padding:20px;
 
}
.helpTexta{
  padding-left:20px;
  background-color: #2020228e
}
.helpText a{
  color:rgb(78, 70, 70);
  font-weight: 600;
  text-decoration: underline
}
/* h4{text-decoration: underline} */
.card-bod {
  padding:10px
}
.list-group{
  padding: 2px
} .list-group-item{
  background: transparent;
  border: none ;text-align: right;
}
.list-group-item:hover{
  background: transparent;
  color: #61dafb
}

.postData{
  -webkit-transition:all .2s linear;
  transition:all .2s linear;
}
.hidden{-webkit-animation: 500ms;animation: 500ms;
  -webkit-transition:all .3s linear;
  transition:all .3s linear;
  overflow: hidden;
  padding: 5px;
  background-color: none;
  height: 0;
  border:none;
}
/* @keyframes */

.postData:hover .hidden{
  /* background-color: #61dafb; */
  height:130px;padding:10px;
}
#siteHead{
  display:inline-block;
  color:white;
  font-size:0.8em
}
.nav-link{
  color:white;font-weight: 500
}
.nav-link:hover{
  color:rgb(117, 165, 255)
}
.intro, .intro .logo{
  text-align: center;
}
.card-div{
  width:100%;
}
.card .row{
  padding:30px;
}
.tags{
  padding:20px;
}
.adDiv{
  width:100%
}
.adDiv{
  padding:10px;
}
.card { 

  border: .3px solid rgb(16, 17, 26);
  border-left:none;border-right:none;
  position: relative;
  padding: 5px;
}
@media(min-width:900px){
  .card .row{
    padding:0;
  }
  .logo{
    display:inline-block;text-align:auto
     
  }
  .nav{
    display:inline-block; padding-left:500px;
  }
 
  #siteHead, .nav-link{
   font-size: 1.2em
  }
  .card-div{
    width:60%;
    padding:10px 12px 0 0 
  }
  .card{
    
    background-color: inherit;
  
    position: relative;
    
  }
  .card-bod{
    display: inline-block;
   vertical-align: middle;
    width: 50%;
  }
 .adDiv{
   width:30%;
   visibility: hidden;
 }
  .card-img{
    width:40%; margin-right: 10px
  }
}

 

   
.post {
    background-color: rgb(243, 249, 255);
}
.flip{
    -webkit-transform:rotateX(180deg);
            transform:rotateX(180deg)
}
.hideComments::before{
    content:'Hide Comments'
}
.showComments::before{
    content:'Show Comments'
}
.btn:focus{
    border: 1px solid rgba(0,0,0,.125);box-shadow: none
}
.overlay {
    background-color: #12121873;
    opacity: .8;
    position: absolute;
    width:inherit; height:inherit
}
.btn-link{
    padding:none
}

.postHead {
    position: absolute;
    top: 0;
    width: 100%
}

article p {
    font-size: 1.05em;
    line-height: 2em;
}

@media(min-width:900px) {
    .post-cont {
        display: -webkit-flex;
        display: flex;
    }

    .half {
        padding: 20px;
        width: 50%
    }
}
.con {
    height: 100vh;
    margin:20px 0 20px 0
}
.quill{
    height:100%
}
#editerId{
    background-color: #ffffffd8
}


.blue{
    color:rgb(50, 167, 79)
}












