.post {
    background-color: rgb(243, 249, 255);
}
.flip{
    transform:rotateX(180deg)
}
.hideComments::before{
    content:'Hide Comments'
}
.showComments::before{
    content:'Show Comments'
}
.btn:focus{
    border: 1px solid rgba(0,0,0,.125);box-shadow: none
}
.overlay {
    background-color: #12121873;
    opacity: .8;
    position: absolute;
    width:inherit; height:inherit
}
.btn-link{
    padding:none
}

.postHead {
    position: absolute;
    top: 0;
    width: 100%
}

article p {
    font-size: 1.05em;
    line-height: 2em;
}

@media(min-width:900px) {
    .post-cont {
        display: flex;
    }

    .half {
        padding: 20px;
        width: 50%
    }
}